.regis-list{
	padding: 0 .17rem;
	margin-top: 1.07rem;
	line-height: 1;
	position: relative;
	
	.item-wrap{
		@include display-flex();
		justify-content: space-between;
		flex-direction: row;
		background: url(../images/qdline.jpg) repeat-x 0 .35rem;
		margin-bottom: .65rem;

		.list-item{
			width: .74rem;

			.jd-count{
				width: .74rem;
				height: .74rem;
				border-radius: 50%;
				background-color: #bf272c;
				text-align: center;
				line-height: .74rem;
				font-size: .2rem;
				color: #d36b6d;
				margin-bottom: .2rem;
				@include box-sizing(border-box);
			}

			.date{
				font-size: .16rem;
				color: #fff;
				text-align: center;
			}

			&.seted .jd-count{
				background-color: #fff;
				color: #f22135;
			}

			&.today.seted .jd-count{
				border: .04rem solid #fff45a;
				background: #bf282c url(../images/todayset.jpg) no-repeat center center;
				background-size: .34rem auto;
			}
		}
	}

	.seven-daytips{
		height: .5rem;
		position: absolute;
		right: .28rem;
		top: -.6rem;
		padding-right: .52rem;
		line-height: .5rem;

		p{
			font-size: .2rem;
			color: #feb104;
		}

		.gift-icon{
			position: absolute;
			right: 0;
			top: 0;
			width: .44rem;
			height: .5rem;
			background: url(../images/img-gift@3x.png) no-repeat left top;
			background-size: 100% auto;
		}
	}
}

.set-btn-wrap{
	text-align: center;
	font-size: 0;

	.set-btn{
		&.qd{
			width: 2.92rem;
			height: .7rem;
			background: url(../images/qdbtn.jpg) no-repeat left center;
			background-size: 100% auto;
			margin: 0 auto;
		}

		&.qdcomplete{
			padding: .2rem .85rem;
			font-size: .25rem;
			color: #fff;
			line-height: 1;
			border: 1px solid #fff;
			border-radius: .3rem;
			display: inline-block;
		}
	}
}